@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  
  html {
    @apply bg-db;
    font-family: 'Manrope', sans-serif;
  }

  .advice-cont {
    width: 45%;
    height: 50%;
    border-radius: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .number {
    font-weight: 400;
  }

  .advice-text {
    font-weight: 800;
  }

  .dice {
    position: absolute;
    margin-top: 23rem;
  }

  .dice:hover {
    box-shadow: 0 0 20px 0 hsl(150, 100%, 66%);
  }

  .dice:active {
    box-shadow: 0 0 20px 0 hsl(193, 38%, 86%);
  }

}